import {render} from "../utlis/template";
import { get } from 'jquery';
import debounce from "lodash/debounce";

export function SearchBox(element, container) {
  const containers = document.querySelectorAll(container);
  const DEBOUNCE_TIME = 500;
  const DISABLED_CLASS = 'is-disabled';
  const loader = document.querySelector(element.dataset.loader);
  const form = formFind(element);

  function formFind(element) {
    parent = element.parentNode;
    if(parent.nodeName != 'FORM') {
      formFind(parent);
    }
    return parent;
  }

  function handleLoading(status = 'start') {
    if(status == 'start') {
      form.classList.add(DISABLED_CLASS);
      loader.classList.add('is-active');
    } else {
      form.classList.remove(DISABLED_CLASS);
      loader.classList.remove('is-active');
    }
  }

  function getResults(query) {
    handleLoading();
    get(`${teik.rest_url}wp/v2/search/`,{
      search: query
    }, (res) => {
      let html = '';
      res.forEach((item) => {
        html += render('search-result-template', item);
      });
      _updateContainers(html);
      handleLoading('finished');
    });
  }

  function _updateContainers(template) {
    containers.forEach(element => {
      element.innerHTML = template;
    });
  }
  const deboundedSearch = debounce((value) => {
    getResults(value);
  }, DEBOUNCE_TIME);

  function handleChange(e) {
    deboundedSearch(e.target.value);
  }

  element.addEventListener('keyup', handleChange);
}