import $ from "jquery";
const defaultConfig = {
  nav: '.js-pricing-nav',
  selector: '.js-pricing-nav .js-pricing-nav-trigger',
  filterableSelector: '.js-pricing-filterable',
  noResultsElement: '.js-pricing-no-results'
}
export function LetterFilter({selector, filterableSelector, noResultsElement,nav} = defaultConfig) {
  const $elements = $(selector);
  const $filterables = $(filterableSelector);
  const BUTTON_ACTIVE_CLASS = 'is-active';
  function handleClick(e) {
    e.preventDefault();
    const $target = $(e.currentTarget);
    const letter = $target.data('letter');
    let visibleCount = 0;
    function filterElements(letter) {
      $filterables.each((key, item) => {
        const name = $(item).data('name');
        if(name.toLowerCase().startsWith(letter)) {
          $(item).removeClass('is-hidden');
          visibleCount++;
        } else {
          $(item).addClass('is-hidden');
        }
      });
    }

    filterElements(letter);
    console.log(visibleCount)
    if(visibleCount <= 0) {
      $(noResultsElement).removeClass('is-hidden');
    } else {
      $(noResultsElement).addClass('is-hidden');
    }
    $(`${nav} .${BUTTON_ACTIVE_CLASS}`).removeClass(BUTTON_ACTIVE_CLASS);
    $target.parent().addClass(BUTTON_ACTIVE_CLASS).blur();
  }
  $elements.click(handleClick);

}