import $ from 'jquery';
export function ScrollingButtons(scroller, button) {
  const $scroller = $(scroller);
  const $buttons = $(button);
  const SCROLL_DISTANCE = 30;
  let interval = null;

  function scroll(currentScroll, dir) {
    if(dir == 'forward') {
      $scroller.scrollLeft(currentScroll + SCROLL_DISTANCE);
    }
    if(dir == 'back') {
      $scroller.scrollLeft(currentScroll - SCROLL_DISTANCE);
    }
  }

  function handleArrowClick(e) {
    e.preventDefault();
    const dir = $(e.currentTarget).data('dir');
    interval = setInterval(() => {
      const currentScroll = $scroller.scrollLeft();
      scroll(currentScroll, dir);
    }, 100);
  }
  function handleMouseup(e) {
    e.preventDefault();
    clearInterval(interval);
  }
  $buttons.mousedown(handleArrowClick);
  $buttons.mouseup(handleMouseup);
}