import { tns } from "tiny-slider/src/tiny-slider";

export function Carousel(element, options) {
  const sliders = document.querySelectorAll(element);

  function handleButton(button, target) {
    target.goTo(button.dataset.dir);
  }

  sliders.forEach(item => {
    const buttons = item.querySelectorAll('.js-slider-button');
    const carousel = item.querySelector('.js-slider');
    const slider = tns({
      container: carousel,
      // items: 1,
      controls: false,
      nav: false,
      ...options
    });

    buttons.forEach((button) => {
      button.addEventListener('click', (e) => {
        e.preventDefault();
        handleButton(button, slider);
      });
    });
  });



  // const buttons = document.querySelectorAll('.js-slider-button, ' + element);
  // console.log();
}


Carousel('.js-carousel', {
  items: 1,
});

Carousel('.js-clients', {
  items: 2,
  slideBy: 'page',
  autoplay: false,
  autoplayButtonOutput: false,
  responsive: {
    576: {
      items: 3
    },
    768: {
      items: 4
    },
    992: {
      items: 5
    }
  }
});