import $ from 'jquery';
import "swipebox";
export function Swipebox() {
  const $selector = $('.js-swipebox a');
  $selector.swipebox({
    afterOpen: (e) => {
      if($selector.length == 1) {
        $('#swipebox-bottom-bar').addClass('is-hidden');
      }
    }
  });
}



Swipebox();